<template>
    <div id="loadBox">
        <!-- <div class="tran3D rtanbox fixed z_-1">
            <div :class="[(index+1)%2==0?'cover':'cover_0']" class=" bg-000 dslb txal" v-for='item,index of 19' :key='index'>
                <span class="el-icon-star-off box"></span>
            </div>
        </div> -->
        <div class=" fixed z-index-2 rtanbox" >
            <div v-for='item of height' :key="item" class="fsa">
                <div class="dslb bg-000 " :class='[isloading?"back_box":""]' v-for='item of width' :key='item' :style='{"width":width_+"px","height":height_+"px"}'></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                width:Number((this.getViewportSize.width/25).toFixed(0)),
                height:Number((this.getViewportSize.height/25).toFixed(0)),
                width_:25,
                height_:25,
                // isloading:false
            }
        },
        computed: {
            isloading(){
                return this.$store.state.loading
            }
        },
        watch: {
            // 'this.$store.state.loading'(n, old){
            //     console.log(n)
            //     console.log(old)
            // }
        },
        created() {
            
        },
        mounted() {
            const that = this
            window.onresize = () => {
                return (() => {
                    let width= window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
                    let height= window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
                    that.width = Number((width/this.width_).toFixed(0))
                    that.height = Number((height/this.height_).toFixed(0))
                })()
            }
        },  
        methods: {

        },
    }
</script>

<style scoped lang="less">
    #loadBox {
        color: aliceblue;
    }

    .mb-1{
        margin-bottom: 1px;
    }
    
    .rtanbox {
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
    }

    .cover {
        width: 100px;
        height: 100px;
        line-height: 100px;
        animation: transOne 5s cubic-bezier(1, .8, .3, 0) 0s infinite;
        /* animation: myfirst 6s linear 0s infinite; */
    }
    .cover_0{
        width: 100px;
        height: 100px;
        line-height: 100px;
        animation: transTwo 5s cubic-bezier(1, .7, .5, 0) 0s infinite;
    }
    .box{
        animation: boxRound 1s cubic-bezier(1, 1, 1, 1) 0s infinite;
    }

    .back_box{
        animation: backBox 0.8s cubic-bezier(0, 0, 0, 0) 0s 1;
    }
    .back_box2{
        animation: backBox2 2s cubic-bezier(0, 0, 0, 0) 0s infinite;
    }
    .back_box3{
        animation: backBox3 2s cubic-bezier(0, 0, 0, 0) 0s infinite;
    }
    @keyframes backBox2 {
        0%{
            transform: translateY(0px);
        }
        100%{
            transform: translateY(-110vh);
        }
    }
    @keyframes backBox3 {
        0%{
            transform: translateY(0px);
        }
        100%{
            transform: translateY(110vh);
        }
    }

    @keyframes backBox {
        0%{
            transform: rotate(0deg) scale(1,1);
        }
        50%{
            transform: rotate(360deg) scale(0.5,0.5);
        }
        100%{
            transform: rotate(720deg) scale(0,0);
        }
    }
    
    @keyframes transOne {
        0% {
            transform: rotate(0deg) scale(1, 1) translateY(0px);
        }
        40% {
            transform: rotate(180deg) scale(.5, .5) translateY(50px);
        }
        60% {
            transform: scale(.1, .1) translateY(800px);
        }
        100% {
            transform: translateY(100vh) scale(1, 1) rotate(360deg);
        }
    }

    @keyframes transTwo {
        0% {
            transform: rotate(0deg) translateY(0px);
        }
        40% {
            transform: rotate(180deg) scale(1, 1) translateY(50px);
        }
        60% {
            transform: scale(.5, .5) translateY(800px);
        }
        100% {
            transform: translateY(-100vh) scale(.01, .01) rotate(360deg);
        }
    }

    @keyframes boxRound {
        0%{
            transform: rotate(0deg);
        }
        100%{
            transform: rotate(360deg);
        }
    }

</style>


